/* ============================================
   PRINT STYLES
   ============================================ */

@media print {
  .user-header-gradient,
  .user-controls-section,
  .ant-btn,
  .ant-pagination {
    display: none;
  }

  .user-management-container {
    background: #ffffff;
  }

  .users-table-modern {
    box-shadow: none;
    border: 1px solid #e5e7eb;
  }
}

